@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

/* Removing Margin and Padding from Body of project */
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* Navbar main container : Contains code of navbar background and its background position */
.NavbarMain.active
{
    font-family: 'Work Sans', sans-serif;
    overflow:hidden;
    position: fixed;
    background-color: blue;
    width: 100%;
    z-index: 1000;
    height: 5rem;
}

.NavbarMain.passive
{
    background-color: blue;
    margin-top: 20px;
    margin-right:30px;
    margin-left: 30px;
    border-radius: 10px;  
    font-family: 'Work Sans', sans-serif;
    overflow:hidden;
    position: sticky;
    height: 5rem;
    z-index: 1000;
}

/* Navbar inside container containing items present inside navbar links logo and buttons */
.NavbarMain .NavbarContainer
{
    max-width: 75rem;
    padding: 0 2rem;
    margin: 0 auto;
    display: flex;
    position: relative;
}

/* Code Hustler navbar logo container used to set its postion */
.NavbarMain .NavbarContainer .NavbarLogo
{
    flex: 1;
    display: flex;
    align-items: center;
}

/* designer code of navbar logo */
.NavbarMain .NavbarContainer .NavbarLogo .NavLogo
{
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 2px;
    line-height: 5rem;
}

/* Hustler font in navbar logo */
.NavbarMain .NavbarContainer .NavbarLogo .NavLogo span
{
    font-weight: 300;
}

/**/
.NavbarMain .NavbarContainer .NavbarButton
{
    flex:3;
    display: flex;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLinks
{
    flex:2;
    position: relative;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign
{
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign .navbtn
{
    display: inline-block;
    padding: 0.6rem 1.5rem;
    font-size: 16px;
    font-weight: 550;
    border: 2px solid #fff;
    border-radius: 2rem;
    line-height: 1;
    margin: 0 0.4rem;
    transition: 0.3s;
    text-transform: uppercase;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign .navbtn.solid,
.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign .navbtn.transparent:hover
{
    background-color: #fff;
    color: #69bde7;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign .navbtn.transparent,
.NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign .navbtn.solid:hover
{
    background-color: transparent;
    color:#fff;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLinks ul
{
    display: flex;
    justify-content: center;
    align-items: center;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLinks .nav-link a
{
    line-height: 5rem;
    color:#fff;
    padding: 0 1.5rem;
    font-size: 20px ;
    letter-spacing: 1px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: 0.5s;
}

.NavbarMain .NavbarContainer .NavbarButton .NavbarLinks .nav-link:hover a
{
    transform: scale(1.1);
}

.NavbarMain .NavbarContainer .hamburger-menu-container
{
    flex:1;
    display: none;
    justify-content: flex-end;
    align-items: center;
}

.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu
{
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu div
{
    width: 1.6rem;
    height: 5px;
    border-radius: 3px;
    background-color: #fff;
    position: relative;
    z-index: 1001;
    transition: 0.5s;
}

.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu div::before,
.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu div::after
{
    content: "";
    position: absolute;
    width: inherit;
    height: inherit;
    background-color: #fff;
    border-radius: 3px;
    transition: 0.5s;
}

.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu div::before
{
    transform: translateY(-7px);
}

.NavbarMain .NavbarContainer .hamburger-menu-container .hamburger-menu div::after
{
    transform: translateY(7px);
}


#hamcheck
{
    position: absolute;
    top: 50%;
    right: 2rem;
    transform: translateY(-50%);
    width: 2.5rem;
    height: 2.5rem;
    z-index: 90000;
    cursor: pointer;
    opacity: 0;    
    display: none;
}

#hamcheck:checked ~ .hamburger-menu-container .hamburger-menu div
{
    background-color: transparent;
}

#hamcheck:checked ~ .hamburger-menu-container .hamburger-menu div:before
{
    transform: translateY(0) rotate(-45deg);
}

#hamcheck:checked ~ .hamburger-menu-container .hamburger-menu div:after
{
    transform: translateY(0) rotate(45deg);
}

ul
{
    list-style: none;
}

a
{
    text-decoration: none;
}

@media only screen and (max-width: 1186px) 
{   

    .NavbarMain .NavbarContainer .hamburger-menu-container
    {
        display: flex;
    }

    #hamcheck
    {
        display: block;
    }

    .NavbarMain.passive .NavbarContainer .NavbarButton
    {
        position: fixed;
        height: calc(100vh - 3rem );
        top: 6rem;
        left:0;
        width: 100%;
        background-color: #69bde7;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        transform: translateX(100%);
        transition: 0.65s;
    }

    .NavbarMain.active .NavbarContainer .NavbarButton
    {
        position: fixed;
        height: calc(100vh - 3rem );
        top: 5rem;
        left:0;
        width: 100%;
        background-color: #69bde7;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        transform: translateX(100%);
        transition: 0.65s;
    }

    #hamcheck:checked ~  .NavbarButton
    {
        transform: translateX(0);
    }

    .NavbarMain .NavbarContainer .hamburger-menu-container
    {
        flex:0;
    }

    #hamcheck:checked ~  .NavbarButton
    {
        left:3%;
        right: 3%;
        width: 94%;
    }


    #hamcheck:checked ~ .NavbarButton .nav-link,
    #hamcheck:checked ~ .NavbarButton .NavbarLogSign
    {
        animation: animation 0.5s ease forwards var(--i);
    }

    @keyframes animation 
    {
        from{
            opacity: 0;
            transform: translateY(15px);
        }    
        to{
            opacity: 1;
            transform: translateY(0px);
        }
    }


    .NavbarMain .NavbarContainer .NavbarButton .NavbarLinks
    {
        flex:initial;    
        width: 100%;

    }

    .NavbarMain .NavbarContainer .NavbarButton .NavbarLinks ul
    {
        flex-direction: column;
    }

    .NavbarMain .NavbarContainer .NavbarButton .NavbarLinks .nav-link
    {
        width: 100%;
        opacity: 0;
        transform: translateY(15px);
    }

    .NavbarMain .NavbarContainer .NavbarButton .NavbarLinks .nav-link a
    {
        line-height: 1;
        align-items: center;
        justify-content: center;
        padding: 2.6rem 2rem;
    }

    .NavbarMain .NavbarContainer .NavbarButton .NavbarLinks .nav-link:hover a
    {
        transform: scale(1);
        background-color: #50a9d6;
    }

    .NavbarMain .NavbarContainer .NavbarButton .NavbarLogSign
    {
        width: 100%;
        flex:initial;
        margin-bottom: 5%;
        padding: 1.5rem 1.9rem;
        justify-content: center;
        opacity: 0;
        transform: translateY(15px);
    }
}